* {
  user-select: none;
}

html, body {
  background-color: #000;
  overflow: hidden;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  position: fixed;
}

#canvas-container {
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

canvas {
  width: 100vmin;
  height: 100vmin;
  position: relative;
  top: 0;
  left: 0;
}

#midi-player-container {
  transition: all 1s;
  position: absolute;
  top: 0;
  left: 0;
}

#midi-player-container:hover {
  opacity: 1 !important;
}

